<template>
    <v-dialog
        v-model="dialog"
    >
        <v-card class="pt-3">
            <v-card-text>
                <label></label>
                <v-card-subtitle>
                    {{ item.name }}
                </v-card-subtitle>

                <label></label>
                <v-text-field
                    v-model="item.fixedPrice"
                    label=""
                    placeholder=""
                    class="right-input"
                ></v-text-field>

                <label></label>
                <b-form-spinbutton
                    id="sb-inline"
                    v-model="item.quantity"
                    min="0"
                    size="lg"
                ></b-form-spinbutton>

                <v-checkbox
                    v-model="item.taxFree"
                    label=""
                ></v-checkbox>

                <!-- <v-btn
                    block
                    @click="addSelectedProduct"
                ></v-btn> -->
                <vs-button
                    :disabled="isDisabled"
                    block
                    @click="addSelectedProduct"
                    size="large"
                >{{ btnText }}</vs-button>
            </v-card-text>


        </v-card>
    </v-dialog>
</template>

<script>
import { mapMutations } from 'vuex'
import _ from 'lodash'

export default {
    name: 'AccountAddOptionItem',
    props: {
    },
    data: () => ({
        dialog: false,
        item: {},
        index: null,
        editMode: false,
    }),
    computed: {
        isDisabled () {
            if (this.item.quantity == 0 && !this.editMode) {
                return true
            }
            return false
        },
        btnText () {
            if (this.editMode) return ''
            return ''
        }
    },
    methods: {
        ...mapMutations([
            'pushSelectedProduct',
            'updateSelectedProduct',
            'setSelectedProductSalesId',
            'deleteSelectedProduct',
        ]),
        open (item, idx) {
            this.dialog = true
            this.item = _.cloneDeep(item)
            this.index = idx

            // 2022-08-19 UPDATE11
            if (this.item.quantity >= 1 ) {
                this.editMode = true
            } else {
                this.editMode = false
            }
            console.log('open', this.item)
        },
        close () {
            this.dialog = false
            this.item = {}
            this.index = null
            this.editMode = false
        },
        addSelectedProduct () {
            this.item.fixedPrice = Number(this.item.fixedPrice) 
            if (this.editMode) {
                if (this.item.quantity == 0) {
                    this.deleteSelectedProduct(this.item)
                } else {
                    this.updateSelectedProduct(this.item)
                }
            } else {
                this.pushSelectedProduct(this.item)
            }
            this.$emit('addSelectedProductOpt', this.item, this.index)
            this.setSelectedProductSalesId(this.$route.params.id)
            this.close()
        }
    }
}

</script>

<style lang="scss">
    .right-input input {
        text-align: right;
    }
</style>
