<template>
    <b-modal
        v-model="dialog"
        title=""
        @ok="deleteBottleDetail"
        ok-title=""
        cancel-title=""
    >
        {{ confirmText }}
    </b-modal>
</template>

<script>
import { mapMutations, mapActions } from 'vuex'

export default {
    name: 'DeleteBottleDetailDialogItem',
    props: {
    },
    data: () => ({
        dialog: false,
        bottleDetail: {},
    }),
    computed: {
        confirmText () {
            if (this.bottleDetail.delete_flg) {
                return ''
            } else {
                return ''
            }
        }
    },
    methods: {
        ...mapActions([
            'deleteBottleListAction',
        ]),
        ...mapMutations([
            'deleteCustomerBottle',
        ]),
        open (data) {
            this.dialog = true
            this.bottleDetail = data
        },
        close () {
            this.dialog = false
            this.bottleDetail = {}
        },
        deleteBottleDetail () {
            this.deleteBottleListAction(this.bottleDetail)
            // this.$router.push('/bottle')

            // 
            this.deleteCustomerBottle(this.bottleDetail.id)
            this.$emit('deleteCustomerBottleDetail', this.bottleDetail)
            this.close()
        }
    }
}

</script>

