<template>
    <div class="home_wrap">
        <v-toolbar
            color="blue-grey darken-3"
            dark
            flat
        >
            <v-app-bar-nav-icon
                @click.stop="drawer = !drawer"
            ></v-app-bar-nav-icon>
                <v-tabs
                    v-model="tab"
                    centered
                >
                    <v-tabs-slider color="yellow"></v-tabs-slider>
                    <v-tab
                        v-for="menuItem in menuItems"
                        :key="menuItem"
                    >
                        {{ menuItem }}
                    </v-tab>
                </v-tabs>
        </v-toolbar>
        <v-tabs-items
            v-model="tab"
            class="order_area_wrap"
        >
            <v-tab-item
                v-for="i in 2"
                :key="i"
            >
                <div
                    v-if="i == 1"
                    class="mx-3"
                >
                    <v-row
                    >
                        <!-- <v-col v-if="befItems.length  == 0">
                            <v-card-title>
                                
                            </v-card-title>
                        </v-col> -->
                        <v-col
                            v-for="(item, i) in befItems"
                            :key="i"
                            xs="12"
                            sm="12"
                            md="12"
                            lg="12"
                            class="px-1 py-1 col_order_area"
                        >
                            <v-card
                                dark
                                color="#385F73"
                            >
                                <v-row>
                                    <v-col
                                        cols="5"
                                    >
                                        <v-subheader>
                                            
                                        </v-subheader>
                                        <v-card-title>
                                            {{ item.product.name }}
                                        </v-card-title>
                                    </v-col>
                                    <v-col cols="2">
                                        <v-subheader>
                                            
                                        </v-subheader>
                                        <v-card-title>
                                            {{ item.quantity }}
                                        </v-card-title>
                                    </v-col>
                                    <v-col cols="2">
                                        <v-subheader>
                                            
                                        </v-subheader>
                                        <v-card-title>
                                            {{ item.disp_seat_name }}
                                        </v-card-title>
                                    </v-col>
                                    <v-col cols="2">
                                        <v-subheader>
                                            
                                        </v-subheader>
                                        <v-card-subtitle>
                                            {{ getWaitTime(item.order_time) }}
                                        </v-card-subtitle>
                                    </v-col>
                                    <v-col cols="1" class="ma-0 pa-0">
                                        <div
                                            class="card_order_ok_btn"
                                            @click="orderDone(item)"
                                        >
                                            OK
                                        </div>
                                    </v-col>
                                </v-row>
                            </v-card>
                        </v-col>
                    </v-row>
                </div>


                <div
                    v-else
                    class="mx-3"
                >
                    <v-row
                    >
                        <!-- <v-col v-if="afItems.length  == 0">
                            <v-card-title>
                                
                            </v-card-title>
                        </v-col> -->
                        <v-col
                            v-for="(item, i) in afItems"
                            :key="i"
                            xs="12"
                            sm="12"
                            md="12"
                            lg="12"
                            class="px-1 py-1 col_order_area"
                        >
                        <v-card
                            dark
                            color="#385F73"
                        >
                            <v-row>
                                <v-col
                                    cols="5"
                                >
                                    <v-subheader>
                                        
                                    </v-subheader>
                                    <v-card-title>
                                        {{ item.product.name }}
                                    </v-card-title>
                                </v-col>
                                <v-col cols="2">
                                    <v-subheader>
                                        
                                    </v-subheader>
                                    <v-card-title>
                                        {{ item.quantity }}
                                    </v-card-title>
                                </v-col>
                                <v-col cols="2">
                                    <v-subheader>
                                        
                                    </v-subheader>
                                    <v-card-title>
                                        {{ item.disp_seat_name }}
                                    </v-card-title>
                                </v-col>
                                <v-col cols="2">
                                    <v-subheader>
                                        
                                    </v-subheader>
                                    <v-card-subtitle>
                                        {{ item.end_time }}
                                    </v-card-subtitle>
                                </v-col>
                                <v-col cols="1" class="ma-0 pa-0">
                                    <div
                                        class="card_order_ok_btn"
                                        @click="orderNotDone(item)"
                                    >
                                        
                                    </div>
                                </v-col>
                            </v-row>
                        </v-card>
                        </v-col>
                    </v-row>
                </div>
            </v-tab-item>
        </v-tabs-items>

        <v-navigation-drawer
            v-model="drawer"
            absolute
            temporary
        >
            <v-list-item>
                <v-list-item-avatar>
                    <img
                        src="@/static/img/logo2.jpg"
                    >
                </v-list-item-avatar>
                <v-list-item-content>
                    <v-list-item-title class="text-h6">
                        alpha pos
                    </v-list-item-title>
                    <v-list-item-subtitle>
                        menu
                    </v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>

            <v-divider></v-divider>

            <v-list
                nav
                dense
            >
                <v-list-item-group
                    v-model="group"
                    active-class="deep-purple--text text--accent-4"
                >
                    <v-list-item
                        @click="showLogoutConfirmDialog"
                    >
                        <v-list-item-title></v-list-item-title>
                    </v-list-item>
                </v-list-item-group>
            </v-list>
        </v-navigation-drawer>

        <LogoutConfirmDialog
            ref="logoutConfirmDialog"
        />
    </div>
</template>
<script>
    import dayjs from 'dayjs'
    import { mapGetters, mapActions, mapMutations } from 'vuex'
    import LogoutConfirmDialog from '@/components/kitchen/dialog/LogoutConfirmDialog'
    import _ from 'lodash'

    export default {
        name: 'KitchenHomeItem',
        components: {
            LogoutConfirmDialog,
        },
        props: {
        },
        data: () => ({
            now: null,
            tab: null,
            menuItems: [
              '', ''
            ],
            text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.',
            drawer: false,
            // befItems: [{},{},{},{},{},{},{},{}],
            // afItems: [{},{},{},{},{},{},{},{}],
            befItems: [],
            afItems: [],
            group: null,
        }),
        beforeCreate () {
        },
        created () {
            this.$eventHub.$off('addNewOrder')
            this.$eventHub.$on('addNewOrder', this.addNewOrder)
            this.$eventHub.$off('deleteOrder')
            this.$eventHub.$on('deleteOrder', this.deleteOrder)
            this.$eventHub.$off('closeSalesHeader')
            this.$eventHub.$on('closeSalesHeader', this.closeSalesHeader)
            this.$eventHub.$off('deleteSalesHeader')
            this.$eventHub.$on('deleteSalesHeader', this.deleteSalesHeader)

            console.log('KitchenHome')
            this.$axios({
                method: 'get',
                url: '/api/sales/get_non_end_sales_food_detail/'
            })
            .then(res => {
                console.log(res)
                if (res.data.length <= 7) {
                    let idx = 0
                    for (const d of res.data) {
                        this.$set(this.befItems, idx, d)
                        idx++
                    }
                } else {
                    this.befItems = res.data
                }
            })
            .catch(e => {
                console.log(e)
            })

            this.$axios({
                method: 'get',
                url: '/api/sales/get_end_sales_food_detail/'
            })
            .then(res => {
                console.log(res)
                if (res.data.length <= 7) {
                    let idx = 0
                    for (const d of res.data) {
                        this.$set(this.afItems, idx, d)
                        idx++
                    }
                } else {
                    this.afItems = res.data
                }
            })
            .catch(e => {
                console.log(e)
            })

            setInterval(function() {
                this.now = dayjs()
            }.bind(this), 1000)


            const scheme = window.location.protocol === 'https:' ? 'wss' : 'ws'
            const hostName = process.env.NODE_ENV !== 'production' ? process.env.VUE_APP_BASE_URL : window.location.host
            // const hostName = 'localhost:8000'
            console.log('process.env.NODE_ENV', process.env.NODE_ENV)
            console.log('window.location.host', window.location.host)
            console.log('process.env.VUE_APP_BASE_URL', process.env.VUE_APP_BASE_URL)
            const url = scheme + '://' + hostName + '/ws/order/' + this.$store.state.loginUser + '/'

            if (this.$store.state.isAuth) {
                if (this.ws === undefined || this.ws.readyState !== 1) {
                    this.ws = new WebSocket(url)
                    console.log('this.ws', this.ws)
                }

                this.ws.onmessage = e => {
                    var receiveData = JSON.parse(e.data)
                    console.log('', receiveData)
                    switch (receiveData.type) {
                        case 0:
                            // 
                            this.$eventHub.$emit('addNewOrder', receiveData.content)
                            break
                        case 1:
                            console.log('')
                            this.$eventHub.$emit('updateOrder', receiveData.content)
                            break
                        case 2:
                            console.log('')
                            this.$eventHub.$emit('deleteOrder', receiveData.content)
                            break
                        case 10:
                            console.log('')
                            this.$eventHub.$emit('deleteSalesHeader', receiveData.content)
                            break
                        case 11:
                            // 
                            this.$eventHub.$emit('closeSalesHeader', receiveData.content)
                            break
                        default:
                            break
                    }
                }
            }
        },
        beforeMount () {
        },
        mounted () {
        },
        beforeUpdate () {
        },
        update () {
        },
        beforeDestroy () {
            this.closeWs()
        },
        destoryd () {
        },
        watch: {
        },
        computed: {
            ...mapGetters([
                'seat',
            ]),
        },
        methods: {
            addNewOrder (item) {
                console.log('addNewOrder', item)
                this.befItems.push(item)
                const nt = this.$vs.notification({
                    duration: 10000,
                    progress: 'auto',
                    color: '',
                    position: '', // position
                    width: '50%',
                    title: '',
                    text: `
                        : ${item.product.name}<br>
                        : ${item.quantity}<br>
                        : ${item.order_time}<br>
                        : ${item.disp_seat_name}
                    `
                })
            },
            updateOrder (order) {
                console.log('updateOrder', order)
                // this.befItems = this.befItems.filter(item => item.header.id != order.id)
                // this.afItems = this.afItems.filter(item => item.header.id != order.id)
                const nt = this.$vs.notification({
                    duration: 10000,
                    progress: 'auto',
                    color: '',
                    position: '', // position
                    width: '50%',
                    title: '',
                    text: `
                        : ${order.product.name}<br>
                        : ${order.quantity}<br>
                        : ${order.disp_seat_name}
                    `
                })
            },
            deleteOrder (order) {
                console.log('deleteOrder', order)
                this.befItems = this.befItems.filter(item => item.id != order.id)
                this.afItems = this.afItems.filter(item => item.id != order.id)
                const nt = this.$vs.notification({
                    duration: 10000,
                    progress: 'auto',
                    color: '',
                    position: '', // position
                    width: '50%',
                    title: '',
                    text: `
                        : ${order.product.name}<br>
                        : ${order.quantity}<br>
                        : ${order.disp_seat_name}
                    `
                })
            },
            getWaitTime (orderTime) {
                if (this.now != null) {
                    let diff = this.now.diff(dayjs(orderTime), 'minute')
                    const hour = Math.floor(diff / 60)
                    const min = diff - (hour * 60)
                    let res = min + ''
                    if (hour != 0) {
                        res = hour + '' + res
                    }
                    return res + ''
                }
                return '-'
            },
            orderDone (item) {
                this.$axios({
                    method: 'POST',
                    url: '/api/sales/end_sales_detail/',
                    data: {
                        sales_detail_id: item.id
                    }
                })
                .then(res => {
                    console.log(res)
                    this.befItems = this.befItems.filter(item => item.id !== res.data.id)
                    this.afItems.push(res.data)
                })
                .catch(e => {
                    console.log(e)
                    // 
                })
            },
            orderNotDone (item) {
                this.$axios({
                    method: 'POST',
                    url: '/api/sales/not_end_sales_detail/',
                    data: {
                        sales_detail_id: item.id
                    }
                })
                .then(res => {
                    console.log(res)
                    this.afItems = this.afItems.filter(item => item.id !== res.data.id)
                    this.befItems.push(res.data)
                    console.log('bef this.befItems', this.befItems)
                    this.befItems = this.sortByOrderTime(this.befItems)
                    console.log('af this.befItems', this.befItems)
                })
                .catch(e => {
                    console.log(e)
                    // 
                })
            },
            closeSalesHeader (header) {
                this.befItems = this.befItems.filter(item => item.header.id != header.id)
                this.afItems = this.afItems.filter(item => item.header.id != header.id)
                const nt = this.$vs.notification({
                    duration: 10000,
                    progress: 'auto',
                    color: '',
                    position: '', // position
                    width: '50%',
                    title: '',
                    text: `
                        No: ${header.header_id}<br>
                        : ${header.disp_seat_name}
                    `
                })
            },
            deleteSalesHeader (header) {
                this.befItems = this.befItems.filter(item => item.header.id != header.id)
                this.afItems = this.afItems.filter(item => item.header.id != header.id)
                const nt = this.$vs.notification({
                    duration: 10000,
                    progress: 'auto',
                    color: '',
                    position: '', // position
                    width: '50%',
                    title: '',
                    text: `
                        No: ${header.header_id}<br>
                        : ${header.disp_seat_name}
                    `
                })
            },
            showLogoutConfirmDialog () {
                this.$refs.logoutConfirmDialog.open()
            },
            closeWs () {
                if (this.ws !== undefined && this.ws.readyState !== 3) {
                    this.ws.close()
                }
            },
            sortByOrderTime (array) {
                let arr = _.cloneDeep(array)
                let result = arr.sort(function(a, b) {
                    return (a.order_time < b.order_time) ? -1 : 1
                })
                return result
            }
        },
        mixins: [],
    }
</script>
<style lang="scss" scoped>
    .home_wrap {
        min-height: 100%;

        .order_area_wrap {
            height: 100%;
            background-color: rgba(229, 229, 229, 0.5);
        }

        .col_order_area {
            height: 142px;

            .card_order_area {
                height: 100%;
                overflow: auto;
            }
            .card_order_ok_btn {
                border-left: 1px solid rgba(255,255,255,1);
                // height: 140px;
                line-height: 135px;
                text-align: center;
                // background-color: rgba(71, 71, 71, 0.5);
                cursor: pointer;
            }
            .card_order_ok_btn:hover {
                // background-color: rgba(121, 121, 121, 0.6);
            }
        }
    }
</style>
