<template>
    <div>
        <vs-input
            class="mt-3 mb-0"
            placeholder="No"
            label="No()"
            v-model="customerNo"
            @blur="focusOutCustomerNo"
        >
            <template
                v-if="custonerNoSuccess"
                #message-success
            >
                OK
            </template>
            <template
                v-if="customerNoError"
                #message-danger
            >
                {{ customerNoErrorText }}
            </template>
        </vs-input>

        <v-card
            flat
            class="mt-4"
        >
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-subtitle></v-list-item-subtitle>
                    <v-list-item-title>{{ customerName }}</v-list-item-title>
                </v-list-item-content>
            </v-list-item>
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-subtitle></v-list-item-subtitle>
                    <v-list-item-title>{{ getStrInData(customerAge) }}</v-list-item-title>
                </v-list-item-content>
                <v-list-item-content>
                    <v-list-item-subtitle></v-list-item-subtitle>
                    <v-list-item-title>{{ customerRank }}</v-list-item-title>
                </v-list-item-content>
            </v-list-item>
        </v-card>
    </div>
</template>

<script>

import utilsMixin from '@/mixins/utils'

export default {
    name: 'SearchCustomerInfoItem',
    props: {
    },
    data: () => ({
        customerNo: '',
        customerInfo: null,
        customerNoError: false,
        customerNoErrorText: '',
        custonerNoSuccess: false,
    }),
    components: {
    },
    computed: {
        customerName () {
            if (this.customerInfo != null) {
                return this.customerInfo.name
            }
            return '-'
        },
        customerAge () {
            if (this.customerInfo != null) {
                return this.customerInfo.age
            }
            return '-'
        },
        customerRank () {
            if (this.customerInfo != null) {
                return this.customerInfo.rank_name
            }
            return '-'
        },
    },
    methods: {
        toHome () {
            this.$router.push({
                name: 'AccountHome'
            })
        },
        focusOutCustomerNo () {
            if (this.customerNo != '') {
                const reg = /^[0-9]+$/
                if (this.customerNo <= 0 || !reg.test(this.customerNo)) {
                    this.showError('No')
                } else {
                    this.customerNoError = false
                    this.$axios({
                        method: 'get',
                        url: '/api/customer/search_customer_by_customer_no/',
                        params: {
                            customer_no: this.customerNo
                        }
                    })
                    .then(res => {
                        console.log(res)
                        this.customerInfo = res.data.data
                        this.custonerNoSuccess = true
                        this.$emit('updateCustomerInfo', this.customerInfo)
                    })
                    .catch(e => {
                        console.log(e)
                        this.showError('')
                        this.$emit('updateCustomerInfo', null)
                    })
                }

            } else {
                this.custonerNoSuccess = false
                this.hideError()
                this.$emit('updateCustomerInfo', null)
            }
        },
        showError (text) {
            this.custonerNoSuccess = false
            this.customerNoError = true
            this.customerInfo = null
            this.customerNoErrorText = text
        },
        hideError () {
            this.customerNoError = false
            this.customerInfo = null
            this.customerNoErrorText = ''
        },
        init () {
            this.customerNo = ''
            this.customerInfo = null
            this.customerNoError = false
            this.customerNoErrorText = ''
            this.custonerNoSuccess = false
        }
    },
    mixins: [
        utilsMixin,
    ],
}
</script>

<style lang="scss" scoped>
    .vs-input-parent::v-deep {
        width: 100%;
        .vs-input {
            width: 100%;
        }
    }
</style>
