<template>
    <b-modal
        v-model="dialog"
        title=""
        @ok="deleteCustomerDetail"
        ok-title=""
        cancel-title=""
    >
        No{{ customerDetail.id }}
    </b-modal>
</template>

<script>
import { mapMutations, mapActions } from 'vuex'

export default {
    name: 'DeleteCustomerDetailDialogItem',
    props: {
    },
    data: () => ({
        dialog: false,
        customerDetail: {},
    }),
    computed: {
    },
    methods: {
        ...mapActions([
            'deleteCustomerListAction',
        ]),
        open (data) {
            this.dialog = true
            this.customerDetail = data
        },
        close () {
            this.dialog = false
            this.customerDetail = {}
        },
        deleteCustomerDetail () {
            this.deleteCustomerListAction(this.customerDetail)
            this.dialog = false
            this.$router.push('/customer')
            this.customerDetail = {}
        }
    }
}

</script>

