<template>
    <v-dialog
        v-model="dialog"
        fullscreen
        persistent
    >
        <v-card
            class="pt-3"
            flat
            v-if="salesData != null"
        >
            <v-toolbar
                dark
                color="primary"
            >
                <v-btn
                    dark
                    icon
                    @click="dialog = false"
                >
                    <v-icon>mdi-close</v-icon>
                </v-btn>
                <v-toolbar-title></v-toolbar-title>
                <v-spacer></v-spacer>
            </v-toolbar>


            <!-- <v-card-title>
                
            </v-card-title> -->

            <v-card-text class="text-right pb-0">
                <span style="font-size: 13px; color: rgba(40, 40, 40, 0.8);">
                    No: {{ salesData.header_id }}
                </span>
            </v-card-text>

            <v-card-text>
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title></v-list-item-title>
                        <v-list-item-subtitle>{{ salesData.visit_time }}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title></v-list-item-title>
                        <v-list-item-subtitle>{{ stayHour }}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>

                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title></v-list-item-title>
                        <v-list-item-subtitle>{{salesData.total_visitors}}: {{salesData.male_visitors}}, : {{salesData.female_visitors}}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title></v-list-item-title>
                        <v-list-item-subtitle>{{ salesData.sales_detail.length }}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
            </v-card-text>

            <v-divider class="mx-5"/>

            <v-card-subtitle class="pb-0">
                : {{ waitOrder.length }}
            </v-card-subtitle>

            <v-card
                v-for="(item, i) in waitOrder"
                :key="i"
                class="mx-5 my-2"
                outlined
            >
                <v-card-text>
                    <label style="font-size: 12px;"></label>
                    <p>{{ item.product.name }}</p>
                    <label style="font-size: 12px;"></label>
                    <p>{{ item.quantity }}</p>
                    <label style="font-size: 12px;"></label>
                    <p>{{ item.order_time }}</p>
                </v-card-text>
            </v-card>

            <v-card-actions>
                <v-btn
                    block
                    depressed
                    outlined
                    color="indigo"
                    @click="dialog = false"
                ></v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
    import dayjs from 'dayjs'

    export default {
        name: 'OrderDetailDialogItem',
        props: {
        },
        data: () => ({
            dialog: false,
            salesData: null,
            // info: null,
            loading: false,
            waitOrder: [],
        }),
        computed: {
            stayHour () {
                const now = dayjs()
                const diff = now.diff(dayjs(this.salesData.visit_time), 'minute')
                const hour = Math.floor(diff / 60)
                const min = diff - (hour * 60)
                return hour + '' + min + ''
            },
        },
        methods: {
            open (salesData) {
                this.waitOrder = []
                this.salesData = null
                this.loading = true
                this.salesData = salesData
                for (const item of salesData.sales_detail) {
                    if (!item.end_flg) {
                        this.waitOrder.push(item)
                    }
                }
                // this.$axios({
                //     method: 'get',
                //     url: `/api/sales/${salesData.id}/`
                // })
                // .then(res => {
                //     console.log(res)
                //     this.order = res.data
                //     this.loading = false
                // })
                // .catch(e => {
                //     console.log(e)
                //     this.loading = false
                // })
                this.dialog = true
            },
            close () {
                this.dialog = false
                this.salesData = null
                // this.info = null
                this.waitOrder = []
            },
        }
    }

</script>

<style lang="scss" scoped>
</style>
